<template>
    <div class="m-pet-rank">
        <div class="m-hot-pets">
            <h3 class="c-sidebar-right-title">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><g fill="#ffa91e"><path d="M324.891 167.266l16.262 94.815L256 217.314l-85.153 44.767 16.262-94.815-68.891-67.16 95.205-13.833L256 0l42.577 86.273 95.205 13.833z"/><path d="M409.944 253.46v87.673L256.9 403.303l-.9-.36-153.944-61.76V253.46L256 315.219z"/><path d="M409.944 362.157v87.673L256.9 512l-.9-.36-153.944-61.76v-87.723L256 423.916z"/></g><g fill="#ff8900"><path d="M341.153 262.081L256 217.314V0l42.577 86.273 95.205 13.833-68.891 67.16zM409.944 253.46v87.673L256.9 403.303l-.9-.36v-87.724zM256 423.916l153.944-61.759v87.673L256.9 512l-.9-.36z"/></g></svg>
                <span>热门宠物</span>
            </h3>
            <div class="u-list" v-if="hot_pets && hot_pets.length">
                <a
                    class="u-pet"
                    v-for="(pet, key) in hot_pets"
                    :key="key"
                    :href="wiki_url(pet.item_id, pet.achievement_id)"
                >
                    <jx3-item-simple :item="pet.item" only-icon="true" icon-size="45px" />
                </a>
            </div>
        </div>
    </div>
</template>

<script>
import ItemSimple from "@jx3box/jx3box-editor/src/ItemSimple";
import { getStatRank } from "@jx3box/jx3box-common/js/stat";
import { get_list } from "@/service/pet";
import { getLink } from "@jx3box/jx3box-common/js/utils";

export default {
    name: "PetRank",
    data() {
        return {
            hot_pets: null,
            hot_pet_views: null,
        };
    },
    created() {
        getStatRank("pet", "views", 20).then((res) => {
            res = res.data;
            this.hot_pet_views = [];
            let source_ids = [];
            res.forEach((item) => {
                if (item.name.startsWith("pet")) {
                    let id = item.name.split("-").pop();
                    source_ids.push(id);
                    this.hot_pet_views[id] = item.value;
                }
            });

            get_list({ ids: source_ids }).then((res) => {
                res = res.data;
                // 按照长度分批
                if (res.code === 200) {
                    let pets = res.data.pets;
                    for (let index in pets) {
                        pets[index].item = {
                            id: pets[index].item_id,
                            IconID: pets[index].IconID,
                            Quality: pets[index].Quality,
                        };
                    }
                    this.hot_pets = pets;
                }
            });
        });
    },
    methods: {
        wiki_url(item_id, achievement_id) {
            if (achievement_id) return getLink("achievement", achievement_id);
            if (item_id) return getLink("item", item_id);
            return null;
        },
    },
    components: {
        "jx3-item-simple": ItemSimple,
    },
};
</script>

<style lang="less">
@import "../../assets/css/components/pet-rank.less";
</style>

<style scope lang="less">
