<template>
    <div class="m-extend-panel">
        <!-- 群号 -->
        <RightSideMsg>
            <em>休闲党交流群</em> :
            <strong
                ><a href="https://jq.qq.com/?_wv=1027&k=InbtJGcw"
                    >658035079</a
                ></strong
            >
        </RightSideMsg>

        <!-- 排行榜 -->
        <div class="m-side-rank">
            <h3 class="c-sidebar-right-title">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><g fill="#ffa91e"><path d="M324.891 167.266l16.262 94.815L256 217.314l-85.153 44.767 16.262-94.815-68.891-67.16 95.205-13.833L256 0l42.577 86.273 95.205 13.833z"/><path d="M409.944 253.46v87.673L256.9 403.303l-.9-.36-153.944-61.76V253.46L256 315.219z"/><path d="M409.944 362.157v87.673L256.9 512l-.9-.36-153.944-61.76v-87.723L256 423.916z"/></g><g fill="#ff8900"><path d="M341.153 262.081L256 217.314V0l42.577 86.273 95.205 13.833-68.891 67.16zM409.944 253.46v87.673L256.9 403.303l-.9-.36v-87.724zM256 423.916l153.944-61.759v87.673L256.9 512l-.9-.36z"/></g></svg>
                热门榜
            </h3>
            <el-tabs v-model="tab">
                <el-tab-pane label="全部" name="knowledge"></el-tab-pane>
                <el-tab-pane label="事件" name="bigbang"></el-tab-pane>
                <el-tab-pane label="八卦" name="gossip"></el-tab-pane>
                <el-tab-pane label="组织" name="organization"></el-tab-pane>
                <el-tab-pane label="玩家" name="player"></el-tab-pane>
            </el-tabs>
            <ul class="m-rank-list" v-if="data && data.length">
                <li v-for="(item, i) in data" :key="i">
                    <router-link
                        class="u-link"
                        :to="{ name: 'view', params: { source_id: item.id } }"
                    >
                        <span
                            class="u-order"
                            :class="highlight(i)"
                            v-text="i + 1"
                        ></span>
                        <span class="u-name" v-text="item.name"></span>
                        <span class="u-per">
                            <em class="u-count" v-text="'+ ' + views[i]"></em>
                        </span>
                    </router-link>
                </li>
            </ul>
        </div>

        <!-- 热门宠物 -->
        <PetRank />
    </div>
</template>

<script>
import PetRank from "./PetRank";
import { getStatRank } from "@jx3box/jx3box-common/js/stat";
import { get_list } from "../../service/knowledge";

export default {
    name: "Extend",
    props: [],
    data: function() {
        return {
            tab: "knowledge",
            data: [],
            views: [],
        };
    },
    computed: {},
    methods: {
        highlight: function(i) {
            if (i == 0) {
                return "t1";
            } else if (i == 1) {
                return "t2";
            } else if (i == 2) {
                return "t3";
            }
        },
        loadData: function() {
            let type = this.tab
            getStatRank(type).then((res) => {
                let result = res && res.data;
                if (result && result.length) {
                    let list = [];
                    let views = [];
                    result.forEach((item) => {
                        if (item.name.startsWith(type)) {
                            let id = item.name.split("-").pop();
                            list.push(id);
                            views.push(item.value["7days"]);
                        }
                    });
                    this.views = views;

                    let data = [];
                    get_list({
                        ids: list,
                    }).then((res) => {
                        res.data.data.data.forEach((item, i) => {
                            if (item) {
                                item.views = this.views[i];
                                data.push(item);
                            }
                        });
                        this.data = data;
                    });
                }else{
                    this.data = []
                }
            });
        },
    },
    watch:{
        tab : function (){
            this.loadData();
        }
    },
    mounted: function() {
        this.loadData();
    },
    components: {
      PetRank,
    },
};
</script>

